













import Vue from 'vue'
import tableinfo from '@/components/table/index.vue'
import Component from 'vue-class-component';
@Component({
  components: {
    tableinfo
  }
})
export default class historyWarning extends Vue{
  private curPage: number = 1;
  private option: any = {
    tableData: [],
    tableCol: [
      {
        label: 'IP',
        prop: 'client_ip'
      },
      {
        label: '告警类型',
        prop: 'alarm_type'
      },
      {
        label: '内容',
        prop: 'alarm_msg'
      },
      {
        label: '第一次告警时间',
        prop: 'first_alarm_time'
      },
      {
        label: '最后告警时间',
        prop: 'last_alarm_time'
      }
    ],
    tableColOperate: [
      {
        label: "详情",
        custom: true,
        name: 'detail'
      },
      {
        label: "处理",
        custom: true,
        name: 'handle',
        type: 'warning'
      }
    ],
    limit: 10,
    pageSize: 0,
    searchOperate: [
      {
        type: "add",
        icon: "el-icon-search",
        label: "查询",
        setData: async (data: any) => {
          if (data.selectinput_value && !data.selectinput_label) {
            this.$message({
              showClose: true,
              message: "请输入查找项！",
              type: "error"
            });
            return;
          }
          this.curPage = 1;
          this.getHistoryWarning(data);
        }
      }
    ],
    searchForm: [
      {
        label: "告警类型",
        value: "alarm_type_id",
        type: "selected",
        prop: "alarm_type_id",
        options: [
          {label: ''},
          {label: '解析响应时延', value: 1},
          {label: '解析成功率', value: 2},
          {label: 'QPS', value: 3},
          {label: '离线检测', value: 4},
          {label: '磁盘空间使用率', value:5},
          {label: '内存使用率', value:6},
          {label: 'CPU使用率', value:7},
        ]
      },
      {
        label: "IP地址",
        value: "ip",
        type: "input",
        prop: "client_ip"
      }
    ],
  }
  private warningDialogVisible = false;
  private option1: any = {
    tableData: [],
    tableCol: [
      {
        label: '类型',
        prop: 'alarm_type'
      },
      {
        label: '内容',
        prop: 'alarm_msg'
      },
      {
        label: '时间',
        prop: 'alarm_time'
      }
    ],
    limit: 10,
    pageSize: 0
  }
  private curPage1 = 1;
  private curRow = null;
  private changePage1 (page: number) {
    this.curPage1 = page;
    this.getWarningDetail(this.curRow);
  }
  private handleChangePage (page: number) {
    this.curPage = page;
    this.getHistoryWarning();
  }
  private async closeWarning (row: any) {
    this.$confirm('是否确认？', '提示', {
      confirmButtonText: '确定',
      cancelButtonText: '取消'
    }).then(() => {
      this.closeWarningConfirm(row);
    })
  }
  private async getWarningDetail (row: any) {
    console.log(row)
    this.warningDialogVisible = true;
    this.curRow = row;
    let res = await this.$http.get('/python/alarm_list', {
      "action": "findData",
      "limit": 10,
      "page": 1,
      "whereJson": {"client_ip": row.client_ip, "alarm_type_id": row.client_type_id, "status": 1},
      "sortJson": {"alarm_time": -1},
    })
    if (res) {
      this.option1.tableData = res.rows;
      this.option1.pageSize = res.count;
    }
  }
  private async closeWarningConfirm (row: any) {
    let res = this.$http.post('/python/alarm_list', {
      "action": "updateData",
      "whereJson": {"status": 1, "client_ip": row.client_ip, "alarm_type_id":row.alarm_type_id},
      "updateJson": {"status":0},
    });
    if (res) {
      this.getHistoryWarning();
    }
  }
  private async getHistoryWarning(conditions?: any) {
    let whereJson = {
      status: '1',
      group: 'true'
    }
    if (conditions) {
      whereJson = Object.assign(whereJson, conditions);
    }
    let res = await this.$http.get('/python/alarm_list', {
      action: 'findData',
      page: this.curPage,
      whereJson
    });
    this.option.pageSize = res.count
    this.option.tableData = res.rows;
  }
  mounted() {
    this.getHistoryWarning();
  }
}
