
























































































































































































































































































import { Component, Prop, Vue, Watch } from "vue-property-decorator";
@Component
export default class TableInfo extends Vue {
  private isDetailShow = false;
  private isOptionshow: boolean = false;
  private isTableColshow: boolean = false;
  private btnTitle: string = "";
  private addform: object = {};
  private btnType: number = 0;
  private searchData: {[index:string]:any} = {};
  // @param options 表格顶部按钮,操作
  // @param tableCol 表头
  // @param tableColOperate 表格额外操作
  // @param tableData 表格数据
  // @param searchForm 搜索数据
  // @param pageSize 单页数量
  // @param pageLimit 总数
  // @param searchOperate 查询列条件
  @Prop() public options!: any[];
  @Prop() public tableCol!: any[];
  @Prop() public tableColOperate!: any[];
  @Prop() public tableData!: any[];
  @Prop() public searchForm!: any[];
  @Prop() public pageSize!: number;
  @Prop() public pageLimit!: number;
  @Prop() public pageArr!: number[];
  @Prop() public searchOperate!: any[];
  @Prop() public setQs!: any[];
  get tabelColWidth() {
    return this.tableColOperate.length * 80;
  }
  private rules: object = {
    Email: [
      { required: true, message: "请输入邮箱地址", trigger: "blur" },
      {
        type: "email",
        message: "请输入正确的邮箱地址",
        trigger: ["blur", "change"]
      }
    ],
    password: { required: true, message: "请输入密码", trigger: "blur" }
  };
  private objDeepCopy(source: any) {
    var sourceCopy = source instanceof Array ? [] : {};
    for (var item in source) {
      (sourceCopy as any)[item] =
        typeof source[item] === "object"
          ? this.objDeepCopy(source[item])
          : source[item];
    }
    return sourceCopy;
  }
  private rowclick(row: any): void {
    this.$emit("bechoiced", row);
  }
  private btnset(type: string, i: number): void {
    this.btnType = i;
    this.btnTitle = type;
    switch (type) {
      case "详情":
        this.addform = this.objDeepCopy(this.tableData[i]);
        this.isDetailShow = true;
        break;
      case "新增":
        this.addform = {};
        this.isOptionshow = true;
        break;
      case "修改":
        this.addform = this.objDeepCopy(this.tableData[i]);
        this.isTableColshow = true;
        break;
      case "删除":
        this.deleteData(i);
        break;
      default:
        this.comebackTo(type, this.tableCol[i]);
        break;
    }
  }
  private fileList: any = [];
  private updateData(file: any): void {
    console.log(this.fileList)
    console.log(file)
    this.$emit("updateData", file.file);
  }
  // 按钮返回操作
  // @param type 按钮类型
  // @param type 返回数据
  private comebackTo(label: string, data: object): void {
    if (this.btnType === undefined) {
      for (let i = 0; i < this.options.length; i++) {
        if (this.options[i].label == label) {
          this.options[i].setData(data);
        }
      }
    } else {
      for (let i = 0; i < this.tableColOperate.length; i++) {
        if (this.tableColOperate[i].label == label) {
          this.tableColOperate[i].setData(data);
          return;
        }
      }
    }
  }
  //新增数据
  private addData(data: any): void {
    if (arguments.length) {
      for (let k in data) {
        if (typeof data[k] == "object") {
          if (data[k].value) {
            data[k + "_value"] = data[k].value;
            data[k] = data[k].label;
          } else {
            data[k] = this.tableData[this.btnType][k];
          }
        }
      }
      this.comebackTo("新增", data);
    } else {
      this.comebackTo("修改", data);
    }
    this.addform = {};
    this.isOptionshow = false;
  }
  private changeData(data: any): void {
    for (let k in data) {
      if (typeof data[k] == "object") {
        if (data[k].value) {
          data[k + "_value"] = data[k].value;
          data[k] = data[k].label;
        } else {
          data[k] = this.tableData[this.btnType][k];
        }
      }
    }
    if (arguments.length > 1) {
      this.tableData[this.btnType] = data;
      this.comebackTo("修改", data);
    }
    this.addform = {};
    this.isTableColshow = false;
  }
  private deleteData(index: number): void {
    this.$confirm("是否删除?", "提示", {
      confirmButtonText: "确定",
      cancelButtonText: "取消",
      type: "warning"
    }).then(
      () => {
        this.comebackTo("删除", this.tableData.splice(this.btnType, 1)[0]);
      },
      () => {
        this.$message({
          type: "info",
          message: "已取消!"
        });
      }
    );
  }
  private setchange(props: string, data: any) {
    this.$set(this.addform, props, data);
  }
  private searchByTag(i: number = 0): void {
    let data = {};
    for (let k in this.searchData) {
      if (this.searchData[k] != "") {
        (data as any)[k] = this.searchData[k];
      }
    }
    this.searchOperate[i].setData(data);
  }
  private pageChange(k: number): void {
    this.$emit("changePage", k);
  }
  private handleSizeChange(k: number): void {
    this.$emit("changeSize", k);
  }
  private changesort(obj: any) {
    let data: any = {};
    data[obj.prop] = obj.order == "descending" ? -1 : 1;
    this.$emit("besort", data);
  }
}
