


































































import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import lineTab from "@/components/table/line.vue";
import pie from "@/components/table/pie.vue";
import tableinfo from "@/components/table/index.vue";
@Component({
  components: {
    lineTab,
    pie,
    tableinfo
  }
})
export default class Ask extends Vue {
  private dialogTableVisible: boolean = false;
  private searchtime: Date = new Date();
  private searchip: string = "";
  private isReDraw: number = 0;
  private innerTime: string = "";
  private innerIp: string | undefined = "";
  private choiced: any[] = [
    {
      value: "",
      label: "全部"
    }
  ];
  private handleChartClick(data: any) {
    // console.log(data);
    this.askTimesDrill(data.name, this.searchip);
  }
  private async askTimesDrill (time: string, client_ip?: string, page: number = 1) {
    this.innerTime = time;
    this.innerIp = client_ip;
    let res = await this.$http.get("/python/dns_req_list", {
      action: "findData",
      whereJson: {
        create_time: `${this.$moment(this.searchtime).format('YYYY-MM-DD')} ${time}`,
        client_ip: client_ip || undefined,
      },
      limit: 5,
      page
    })
    console.log(res)
    this.dialogTableVisible = true;
    this.option.pageSize = res.count;
    this.option.tableData = res.rows;
  }
  private option = {
    tableCol: [
      {
        prop: "client_host",
        label: "host",
        width: 200
      },
      {
        prop: "client_ip",
        label: "IP",
        width: 130
      },
      {
        prop: "client_port",
        label: "port",
        width: 100
      },
      {
        prop: "type_name",
        label: "类型",
        width: 100
      },
      {
        prop: "log_time",
        label: "时间",
        width: 200
      }
    ],
    pageSize: 10,
    pageLimit: 5,
    tableData: []
  }
  private async searchBytime() {
    let time = this.searchtime;
    let month = time.getMonth() + 1;
    let getmonth = month > 9 ? month : "0" + month;
    let date = time.getDate() > 9 ? time.getDate() : "0" + time.getDate();
    let searchdata: any = {
      time: `${time.getFullYear()}-${getmonth}-${date}`
    };
    if (this.searchip.length) {
      searchdata["client_ip"] = this.searchip;
    }
    let res = await this.$http.get("/python/dns_req_list", {
      action: "findData",
      whereJson: searchdata
    });
    let arr = [],
      arr1 = [],
      arr2 = [];
    for (let i = 0; i < res.info.type_name.length; i++) {
      if (res.info.type_name[i] != "ALL") {
        arr1.push({
          value: res.info.rate[i],
          name: res.info.type_name[i]
        });
        arr2.push({
          name: res.info.type_name[i],
          type: "line",
          data: res.info[res.info.type_name[i]]
        });
      } else {
        arr.push({
          name: "DNS查询次数",
          type: "line",
          data: res.info[res.info.type_name[i]]
        });
      }
    }
    console.log(arr);
    this.content = arr;
    this.content1 = arr2;
    this.piedata = arr1;
    this.rulers = res.info.time;
    this.isReDraw++;
  }
  private piedata: any[] = [];
  private color: string[] = [
    "#FFB6C1",
    "#7B68EE",
    "#BA55D3",
    "#4169E1",
    "	#00FFFF"
  ];
  private searchData: object = {
    size: 1,
    time: [],
    device: 1
  };
  private content: any[] = [];
  private rulers: string[] = [];
  private content1: any[] = [];
  private rulers1: string[] = [];
  private activeIndex: string = "1";
  async mounted() {
    let time = new Date();
    let month = time.getMonth() + 1;
    let getmonth = month > 9 ? month : "0" + month;
    let date = time.getDate() > 9 ? time.getDate() : "0" + time.getDate();
    let rows = await this.$http.get("/python/device_list", {
      action: "findData",
      limit: 10000,
      page: 1
    });
    for (let i = 0; i < rows.rows.length; i++) {
      this.choiced.push({
        label: rows.rows[i].ip,
        value: rows.rows[i].ip
      });
    }
    let res = await this.$http.get("/python/dns_req_list", {
      action: "findData",
      whereJson: { time: `${time.getFullYear()}-${getmonth}-${date}` }
    });
    let arr = [],
      arr1 = [],
      arr2 = [];
    for (let i = 0; i < res.info.type_name.length; i++) {
      if (res.info.type_name[i] != "ALL") {
        arr1.push({
          value: res.info.rate[i],
          name: res.info.type_name[i]
        });
        arr2.push({
          name: res.info.type_name[i],
          type: "line",
          data: res.info[res.info.type_name[i]]
        });
      } else {
        arr.push({
          name: "DNS查询次数",
          type: "line",
          data: res.info[res.info.type_name[i]]
        });
      }
    }
    console.log(arr, 'mount')
    this.content = arr;
    this.content1 = arr2;
    this.piedata = arr1;
    this.rulers = res.info.time;
  }
}
