
















import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import tableinfo from "@/components/table/index.vue";
import session from "@/utils/sessionStorage";
@Component({
  components: {
    tableinfo
  }
})
export default class UserMsg extends Vue {
  private activeName: string = "first";
  public option: optionObj = {
    tableCol: [
      {
        prop: "client_ip",
        label: "用户IP地址"
      },
      {
        prop: "phone",
        label: "用户归属"
      },
      {
        prop: "num",
        label: "解析次数"
      },
      {
        prop: "num_host",
        label: "请求域名个数"
      },
      {
        prop: "num_err",
        label: "错误次数"
      },
      {
        prop: "err_rate",
        label: "错误率"
      }
    ],
    pageSize: 10,
    pageArr: [20, 50, 100],
    pageLimit: 20,
    tableData: [],
    searchForm: [
      {
        type: "input",
        label: "IP",
        prop: "client_ip"
      }
    ],
    searchOperate: [
      {
        type: "add",
        icon: "el-icon-search",
        label: "查询",
        setData: async (data: any) => {
          data.type = "ip";
          let res = await this.$http.get("/python/top_list", {
            action: "findData",
            whereJson: data,
            limit: this.pageLimit1,
            page: this.page1
          });
          for (let i = 0; i < res.rows.length; i++) {
            res.rows[i].err_rate =
              (res.rows[i].err_rate * 100).toFixed(0) + "%";
          }
          this.option.tableData = res.rows;
          this.option.pageSize = res.count;
        }
      }
    ]
  };
  private searchIP: string = "";
  public option1: optionObj = {
    tableCol: [
      {
        prop: "client_host",
        label: "域名"
      },
      {
        prop: "num",
        label: "DNS查询次数",
        sortable: true
      },
      {
        prop: "num_err",
        label: "错误次数",
        sortable: true
      },
      {
        prop: "err_rate",
        label: "错误率",
        sortable: true
      }
    ],
    pageArr: [20, 50, 100],
    pageSize: 10,
    pageLimit: 20,
    tableData: [],
    searchForm: [
      {
        type: "input",
        label: "域名",
        prop: "client_host"
      }
    ],
    searchOperate: [
      {
        type: "add",
        icon: "el-icon-search",
        label: "查询",
        setData: async (data: any) => {
          data.type = "host";
          let res = await this.$http.get("/python/top_list", {
            action: "findData",
            whereJson: data,
            limit: this.pageLimit,
            page: this.page1
          });
          for (let i = 0; i < res.rows.length; i++) {
            res.rows[i].err_rate =
              (res.rows[i].err_rate * 100).toFixed(0) + "%";
          }
          this.option1.tableData = res.rows;
          this.option1.pageSize = res.count;
        }
      }
    ]
  };
  async mounted() {
    let res = await this.$http.get("/python/top_list", {
      action: "findData",
      whereJson: { type: "host" },
      limit: 20,
      page: 1
    });
    for (let i = 0; i < res.rows.length; i++) {
      res.rows[i].err_rate = (res.rows[i].err_rate * 100).toFixed(0) + "%";
    }
    this.option1.tableData = res.rows;
    this.option1.pageSize = res.count;
    let res1 = await this.$http.get("/python/top_list", {
      action: "findData",
      whereJson: { type: "ip" },
      limit: 20,
      page: 1
    });
    for (let i = 0; i < res1.rows.length; i++) {
      res1.rows[i].err_rate = (res1.rows[i].err_rate * 100).toFixed(0) + "%";
    }
    this.option.tableData = res1.rows;
    this.option.pageSize = res1.count;
  }
  private async searchBytime() {
    if (this.searchIP.length) {
      let res = await this.$http.get("/python/top_list", {
        action: "findData",
        whereJson: { type: "ip", client_ip: this.searchIP },
        limit: 20,
        page: 1
      });
      for (let i = 0; i < res.rows.length; i++) {
        res.rows[i].err_rate = (res.rows[i].err_rate * 100).toFixed(0) + "%";
      }
      this.option.tableData = res.rows;
      this.option.pageSize = res.count;
    }
  }
  private pageLimit: number = 20;
  private pageLimit1: number = 20;
  private page: number = 1;
  private page1: number = 1;
  private async changeSize1(k: number) {
    this.pageLimit = k;
    let res = await this.$http.get("/python/top_list", {
      action: "findData",
      whereJson: { type: "host" },
      limit: this.pageLimit,
      page: this.page1
    });
    for (let i = 0; i < res.rows.length; i++) {
      res.rows[i].err_rate = (res.rows[i].err_rate * 100).toFixed(0) + "%";
    }
    this.option1.tableData = res.rows;
  }
  private async changePage1(k: number) {
    this.page1 = k;
    let res = await this.$http.get("/python/top_list", {
      action: "findData",
      whereJson: { type: "host" },
      limit: this.pageLimit,
      page: k
    });
    for (let i = 0; i < res.rows.length; i++) {
      res.rows[i].err_rate = (res.rows[i].err_rate * 100).toFixed(0) + "%";
    }
    this.option1.tableData = res.rows;
  }
  private async besort1(obj: any) {
    let res = await this.$http.get("/python/top_list", {
      action: "findData",
      whereJson: { type: "host" },
      limit: this.pageLimit,
      sortJson: obj,
      page: this.page1
    });
    for (let i = 0; i < res.rows.length; i++) {
      res.rows[i].err_rate = (res.rows[i].err_rate * 100).toFixed(0) + "%";
    }
    this.option1.tableData = res.rows;
  }
  private async changePage(k: number) {
    this.page = k;
    let res = await this.$http.get("/python/top_list", {
      action: "findData",
      whereJson: { type: "ip" },
      limit: this.pageLimit1,
      page: this.page1
    });
    for (let i = 0; i < res.rows.length; i++) {
      res.rows[i].err_rate = (res.rows[i].err_rate * 100).toFixed(0) + "%";
    }
    this.option.tableData = res.rows;
  }
  private async changeSize(k: number) {
    this.pageLimit1 = k;
    let res = await this.$http.get("/python/top_list", {
      action: "findData",
      whereJson: { type: "ip" },
      limit: this.pageLimit1,
      page: this.page
    });
    for (let i = 0; i < res.rows.length; i++) {
      res.rows[i].err_rate = (res.rows[i].err_rate * 100).toFixed(0) + "%";
    }
    this.option.tableData = res.rows;
  }
}
interface optionObj {
  tableCol: any;
  pageArr: any;
  pageSize: any;
  pageLimit: any;
  tableData: any;
  searchForm: any;
  searchOperate: any;
}
