
































import { Component, Vue } from "vue-property-decorator";
import session from "@/utils/sessionStorage";
@Component
export default class Header extends Vue {
  created() {}
  private async signout() {
    let res = await this.$http.post("python/login", {
      action: "SignOut",
      username: this.username,
      tocken: session.fetch("name")
    });
    this.$message({
      showClose: true,
      message: "退出成功",
      type: "success"
    });
    session.delete("name");
    session.delete("tocken");
    this.$router.push("/login");
  }
  private validatePass = (rule: any, value: string, callback: any) => {
    if (value != this.psw.newpsw) {
      callback(new Error("两次密码输入不一致！"));
    } else {
      callback();
    }
  };
  private closeLog(): void {
    this.psw = {};
    this.isDiolgshow = false;
  }
  private username: string = session.fetch("name");
  public ruler: object = {
    password: [{ required: true, message: "原密码不能为空" }],
    newpsw: [{ required: true, message: "新密码不能为空" }],
    repsw: [
      { required: true, message: "再次输入新密码" },
      { validator: this.validatePass, trigger: "blur" }
    ]
  };
  private changepsw(): void {
    this.isDiolgshow = true;
  }
  private updatePsw(): void {
    (this.$refs["psw"] as any).validate(async (valid: any) => {
      if (valid) {
        let res = await this.$http.post("python/login", {
          action: "updateData",
          whereJson: { username: this.username, password: this.psw.password },
          updateJson: { password: this.psw.newpsw }
        });
        this.psw = {};
        this.isDiolgshow = false;
        this.$message({
          showClose: true,
          message: "修改成功，请重新登陆!",
          type: "success"
        });
        session.delete("name");
        session.delete("tocken");
        this.$router.push("/login");
      } else {
        console.log("error submit!!");
      }
    });
  }
  private isDiolgshow: boolean = false;
  private psw: loginField = {};
}
interface loginField {
    password?: String;
    newpsw?: String;
}
