






import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import tableinfo from "@/components/table/index.vue";
@Component({
  components: {
    tableinfo
  }
})
export default class equManagement extends Vue {
  public option: any = {
    options: [
      {
        type: "add",
        icon: "el-icon-plus",
        label: "新增",
        setData: async (data: any) => {
          let res = await this.$http.post("/python/device_list", {
            action: "insertData",
            dataArr: [data]
          });
          if (res) {
            this.$message({
              type: "success",
              message: "新增成功!"
            });
            this.refreshData(1);
          }
        }
      },
      {
        type: "add",
        icon: "el-icon-download",
        label: "导出",
        setData: () => {
          this.$http.down("python/device_list");
        }
      },
      {
        type: "upload",
        icon: "el-icon-upload2",
        label: "导入"
      }
    ],
    tableCol: [
      {
        prop: "code",
        label: "设备编号",
        width: 130,
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "name",
        label: "设备名称",
        width: 100,
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "address",
        label: "地址",
        width: 200,
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "cpu",
        label: "CPU",
        width: 100,
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "device_type",
        label: "设备类型",
        width: 100,
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "disk",
        label: "磁盘空间",
        width: 100,
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "factory",
        label: "所属厂商",
        width: 100,
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "ip",
        label: "IP地址",
        width: 150,
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "level",
        label: "所属节点",
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "message",
        label: "描述信息",
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "online_time",
        label: "在线时间",
        width: 100,
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "os",
        label: "操作系统",
        width: 100,
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "os_version",
        label: "系统版本",
        width: 100,
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "power",
        label: "功率",
        width: 100,
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "ram",
        label: "RAM",
        width: 100,
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      }
    ],
    pageSize: 5,
    pageLimit: 5,
    tableColOperate: [
      {
        label: "修改",
        setData: async (data: any) => {
          let name = data.name;
          delete data.name;
          delete data.ip;
          let res = await this.$http.post("/python/device_list", {
            action: "updateData",
            whereJson: { name: name },
            updateJson: data
          });
          if (res) {
            this.$message({
              type: "success",
              message: "修改成功!"
            });
            this.refreshData(1);
          }
        }
      },
      {
        label: "删除",
        type: "danger",
        setData: async (data: any) => {
          let res = await this.$http.post("/python/device_list", {
            action: "delData",
            whereJson: { id: [data.id] }
          });
          if (res) {
            this.$message({
              type: "success",
              message: "删除成功!"
            });
            this.refreshData(1);
          }
        }
      }
    ],
    tableData: [],
    searchForm: [
      {
        icon: "el-icon-plus",
        type: "selectinput",
        choiced: [
          {
            label: "设备编号",
            value: "code"
          },
          {
            label: "设备名称",
            value: "name"
          },
          {
            label: "IP地址",
            value: "ip"
          },
          {
            label: "所属节点",
            value: "level"
          },
          {
            label: "所属厂商",
            value: "factory"
          }
        ]
      }
    ],
    searchOperate: [
      {
        type: "add",
        icon: "el-icon-search",
        label: "查询",
        setData: async (data: any) => {
          if (data.selectinput_value && !data.selectinput_label) {
            this.$message({
              showClose: true,
              message: "请输入查找项！",
              type: "error"
            });
            return;
          }
          let searchdata: any = {};
          if (data.selectinput_label) {
            searchdata[data.selectinput_label] = data.selectinput_value;
          }
          let res = await this.$http.get("/python/device_list", {
            action: "findData",
            limit: 10,
            page: 1,
            whereJson: searchdata
          });
          this.option.pageSize = res.count;
          this.option.tableData = res.rows;
        }
      }
    ]
  };
  private async updateData(file: any) {
    let res = await this.$http.update("python/device_list", file);
  }
  private async refreshData(k: number) {
    let res = await this.$http.get("/python/device_list", {
      action: "findData",
      limit: 10,
      page: k
    });
    this.option.pageSize = res.count;
    this.option.tableData = res.rows;
  }
  private async changePage(k: number) {
    this.refreshData(k);
  }
  async created() {
    this.refreshData(1);
  }
}
