<template>
  <div class="column">
    <h3>{{title}}</h3>
    <div
      class="flex"
      style="height:20px;padding:0 30px;color:white;font-size: 12px"
      :style="{color:fontcolor}"
    >
      <div v-for="(t,i) in content" :key="i" class="flex" style="margin:10px">
        <div class="cir" :style="{background:color[i]}"></div>
        <span>&nbsp; {{t.name}}</span>
      </div>
    </div>
    <div
      :id="lineclass"
      :style="{width:canvaswidth+'px',height:canvasheight+'px'}"
      style="margin-top:-40px"
    ></div>
  </div>
</template>
<script>
//折现表 以前的
export default {
  data() {
    return {
      lineclass: "line" + Math.floor(Math.random() * 10000)
    };
  },
  props: {
    min: {
      type: Number || null,
      default: () => {
        return null
      }
    },
    max: {
      type: Number || null,
      default: () => {
        return null
      }
    },
    rulers: Array,
    content: Array,
    canvaswidth: Number,
    isReDraw: {
      type: Number,
      default: () => {
        return 0;
      }
    },
    title: String,
    canvasheight: Number,
    startText: {
      type: String,
      default: () => {
        return "";
      }
    },
    addtext: {
      type: String,
      default: () => {
        return "";
      }
    },
    color: Array,
    endText: {
      type: String,
      default: () => {
        return "";
      }
    },
    fontcolor: {
      type: String,
      default: () => {
        return "black";
      }
    }
  },
  mounted() {
    setTimeout(() => {
      this.drawLine();
    }, 500);
  },
  watch: {
    isReDraw(oldval, newval) {
      this.drawLine();
    }
  },
  methods: {
    drawLine() {
      var echarts = require("echarts");
      var add = this.addtext,
        ruler = this.rulers,
        fontcolor = this.fontcolor;
      // 基于准备好的dom，初始化echarts实例
      echarts.init(document.getElementById(this.lineclass)).clear();
      var myChart = echarts.init(document.getElementById(this.lineclass));
      // 绘制图表
      myChart.setOption({
        tooltip: {
          trigger: "axis"
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true
        },
        xAxis: {
          type: "category",
          axisTick: {
            alignWithLabel: true
          },
          axisLine: {
            onZero: false,
            lineStyle: {
              color: this.fontcolor
            }
          },
          axisPointer: {
            label: {
              formatter: function(params) {
                return "时间  " + params.value;
              }
            }
          },
          data: ruler
        },
        yAxis: {
          type: "value",
          axisLine: {
            lineStyle: {
              color: this.fontcolor
            }
          },
          splitLine: {
            lineStyle: {
              color: "RGBA(220,220,220,0.2)"
            }
          },
          axisLabel: {
            formatter: val => {
              let pas = val.toFixed(2);
              let res = add ? pas + add : val;
              return res;
            }
          },
          min: this.min,
          max: this.max
        },
        series: this.content,
        color: this.color
      });
      myChart.on('click', (params) => {
        this.$emit('chart-click', params)
      })
    }
  }
};
</script>
 <style scoped>
.cir {
  width: 10px;
  height: 10px;
  border-radius: 50%;
}
</style>