






























import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import tableinfo from "@/components/table/index.vue";
import session from "@/utils/sessionStorage";
@Component({
  components: {
    tableinfo
  }
})
export default class UserMsg extends Vue {
  private curRow: any = {};
  private checkedList: Array<number> = [];
  private treeData: any = {};
  private newCheckedList: Array<number> = [];
  private props = {
    label: 'name',
    children: 'children'
  }
  private dialogVisible = false;
  private log(a: any) {
    console.log(a)
  }
  private async setPermission(id: number) {
    let list = (this.$refs['permisstionTree'] as any).getCheckedKeys();
    let res = await this.$http.post("/python/power_list", {
      "action": "updateData",
      "whereJson": {"role_id": id},
      "updateJson": {
        "fn_id": list
      }
    })
    if (res) {
      this.$message({
        type: 'success',
        message: '操作成功'
      })
      this.dialogVisible = false;
    } else {
      this.$message.error('操作失败');
    }
  }
  private async permisstionClick(row: any) {
    this.curRow = row;
    function getChecks (list: Array<any>, res: Array<number>) {
      for (let temp of list) {
        if (temp.checked === "true" && !temp.children.length) {
          res.push(temp.id)
        }
        if (temp.children.length) {
          getChecks(temp.children, res);
        }
      }
    }
    let res = await this.$http.get("/python/power_list", {
      "action": "findData",
      "whereJson": {
        "role_id": row.id
      }
    })
    let checkedList: Array<number> = [];
    getChecks(res.rows, checkedList);
    this.checkedList = checkedList;
    this.treeData = res.rows;
    this.dialogVisible = true;
  }
  private activeName: string = "first";
  public option: any = {
    options: [
      {
        type: "add",
        icon: "el-icon-plus",
        label: "新增",
        setData: async (data: any) => {
          if (data.class_name_value) {
            data.role_id = data.class_name_value;
            delete data.class_name_value;
            delete data.class_name;
          }
          let res = await this.$http.post("/python/user_list", {
            action: "insertData",
            dataArr: [data]
          });
          if (res) {
            this.$message({
              type: "success",
              message: "新增成功!"
            });
            this.refreshData();
          }
        }
      },
      {
        type: "add",
        icon: "el-icon-search",
        label: "查询",
        setData: (data: object) => {}
      }
    ],
    setQs: [
      {
        prop: "username",
        label: "用户名",
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "password",
        label: "密码",
        type: "password",
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "phone",
        label: "手机",
        rules: [
          {
            type: "phone",
            message: "请输入正确的电话号码",
            trigger: ["blur", "change"]
          }
        ]
      },
      {
        prop: "email",
        label: "Email",
        rules: [
          {
            type: "email",
            message: "请输入正确的邮箱地址",
            trigger: ["blur", "change"]
          }
        ]
      },
      {
        prop: "name",
        label: "姓名",
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "class_name",
        label: "用户角色",
        type: "selected",
        options: [],
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "sex",
        label: "性别",
      },
      {
        prop: "age",
        label: "年龄",
      },
      {
        prop: "message",
        label: "备注信息",
      }
    ],
    tableCol: [
      {
        prop: "username",
        label: "用户名",
        width: 100
      },
      {
        prop: "phone",
        label: "手机",
        width: 130
      },
      {
        prop: "email",
        label: "Email",
        width: 200
      },
      {
        prop: "name",
        label: "姓名",
        width: 100
      },
      {
        prop: "class_name",
        label: "用户角色"
      },
      {
        prop: "sex",
        label: "性别",
        width: 100
      },
      {
        prop: "age",
        label: "年龄",
        width: 100
      },
      {
        prop: "message",
        label: "描述信息"
      }
    ],
    pageSize: 5,
    pageLimit: 5,
    tableColOperate: [
      {
        label: "修改",
        setData: async (data: any) => {
          if (data.class_name_value) {
            data.role_id = data.class_name_value;
            delete data.class_name_value;
          }
          let name = data.username;
          delete data.username;
          let res = await this.$http.post("/python/user_list", {
            action: "updateData",
            whereJson: { username: name },
            updateJson: data
          });
          if (res) {
            this.$message({
              type: "success",
              message: "修改成功!"
            });
            this.refreshData();
          }
        }
      },
      {
        label: "删除",
        type: "danger",
        setData: async (data: any) => {
          await this.$http.post("/python/user_list", {
            action: "delData",
            whereJson: { id: [data.id] }
          });
          this.$message({
            type: "success",
            message: "删除成功!"
          });
          this.refreshData();
        }
      }
    ],
    tableData: [],
    searchForm: [
      {
        type: "input",
        label: "用户名称",
        prop: "username"
      },
      {
        type: "selected",
        label: "用户类型",
        prop: "class_name",
        options: []
      },
      {
        type: "input",
        label: "用户状态",
        prop: "status"
      },
      {
        type: "daterange",
        label: "失效时间",
        prop: "time"
      }
    ]
  };
  public option1: any = {
    options: [
      {
        type: "add",
        icon: "el-icon-plus",
        label: "新增",
        setData: async (data: any) => {
          let res = await this.$http.post("/python/role_class", {
            action: "insertData",
