      {
        label: "修改",
        setData: async (data: any) => {
          await this.$http.post("/python/role_class", {
            action: "updateData",
            whereJson: { id: data.id },
            updateJson: { class_name: data.class_name }
          });
          this.$message({
            type: "success",
            message: "修改成功!"
          });
          this.refreshData();
        }
      },
      {
        label: "删除",
        type: "danger",
        setData: async (data: any) => {
          let res = await this.$http.post("/python/role_class", {
            action: "delData",
            whereJson: { id: [data.id] }
          });
          this.$message({
            type: "success",
            message: "删除成功!"
          });
          this.refreshData();
        }
      }
    ],
    tableData: []
  };
  private async changePage(k: number) {
    let data = await this.$http.get("/python/user_list", {
      action: "findData",
      limit: 5,
      page: k
    });
    this.option.tableData = data.rows;
  }
  private async changePage1(k: number) {
    let data = await this.$http.get("/python/role_class", {
      action: "findData",
      limit: 10,
      page: k
    });
    this.option1.tableData = data.rows;
  }
  private async refreshData() {
    let res = await this.$http.get("/python/role_class", {
      action: "findData",
      limit: 10,
      page: 1
    });
    this.option1.pageSize = res.count;
    this.option1.tableData = res.rows;
    for (let i = 0; i < res.rows.length; i++) {
      this.option.setQs[5].options.push({
        label: res.rows[i].class_name,
        value: res.rows[i].id
      });
      this.option.searchForm[1].options.push({
        label: res.rows[i].class_name,
        value: res.rows[i].id
      });
    }
    let data = await this.$http.get("/python/user_list", {
      action: "findData",
      limit: 5,
      page: 1
    });
    this.option.pageSize = data.count;
    this.option.tableData = data.rows;
  }
  async mounted() {
    if (session.fetch("tocken")) {
      this.refreshData();
    }
  }
}
