

































import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import lineTab from "@/components/table/line.vue";
@Component({
  components: {
    lineTab
  }
})
export default class Ask extends Vue {
  private color: string[] = [
    "#FFB6C1",
    "#7B68EE",
    "#BA55D3",
    "#4169E1",
    "	#00FFFF"
  ];
  private content: any[] = [];
  private searchip: string = "";
  private searchtime: Date = new Date();
  private rulers: string[] = [];
  private isReDraw: number = 0;
  private choiced: any[] = [
    {
      value: "",
      label: "全部"
    }
  ];
  async mounted() {
    let time = new Date();
    let month = time.getMonth() + 1;
    let getmonth = month > 9 ? month : "0" + month;
    let date = time.getDate() > 9 ? time.getDate() : "0" + time.getDate();
    let rows = await this.$http.get("/python/device_list", {
      action: "findData",
      limit: 10000,
      page: 1
    });
    for (let i = 0; i < rows.rows.length; i++) {
      this.choiced.push({
        label: rows.rows[i].ip,
        value: rows.rows[i].ip
      });
    }
    let res = await this.$http.get("/python/dns_err_list", {
      action: "findData",
      whereJson: { time: `${time.getFullYear()}-${getmonth}-${date}` }
    });
    for (let i = 0; i < res.info.type_name.length; i++) {
      this.content.push({
        name: res.info.type_name[i],
        type: "line",
        data: res.info[res.info.type_name[i]]
      });
    }
    this.rulers = res.info.time;
  }
  private async searchBytime() {
    let time = this.searchtime;
    let month = time.getMonth() + 1;
    let getmonth = month > 9 ? month : "0" + month;
    let date = time.getDate() > 9 ? time.getDate() : "0" + time.getDate();
    let searchdata = { time: `${time.getFullYear()}-${getmonth}-${date}` }
    if (this.searchip.length) {
      (searchdata as any)["ip"] = this.searchip;
    }
    let res = await this.$http.get("/python/dns_err_list", {
      action: "findData",
      whereJson: searchdata
    });
    let arr = [];
    for (let i = 0; i < res.info.type_name.length; i++) {
      arr.push({
        name: res.info.type_name[i],
        type: "line",
        data: res.info[res.info.type_name[i]]
      });
    }
    this.content = arr;
    this.rulers = res.info.time;
    this.isReDraw++;
  }
}
