import axios from 'axios'
import session from './sessionStorage'
import Vue from 'vue';
let baseUrl: string | undefined = process.env.NODE_ENV !== 'development' ? undefined : "";
baseUrl = `${baseUrl||''}${process.env.NODE_ENV !== 'development' ? '/api' : ''}`;
let _root: any = new Vue();
const instance = axios.create({
    baseURL: baseUrl,
    headers: {
        "Access-Control-Allow-Origin": "*"
    }
})
let loading: any;
// 请求拦截器
instance.interceptors.request.use((config) => {
    return config;
}, (error) => {
    return Promise.reject(error);
})
/**
 * 返回拦截器
 */
instance.interceptors.response.use(
    res => {
        if (loading) {
            loading.close()
        }
        if (res.data.code == 500) {
            _root.$message({
                showClose: true,
                message: res.data.msg,
                type: 'error'
            });
            return
        } else {
            // console.log(res.data);
            return res.data;
        }
    },
    err => {
        if (loading) {
            loading.close()
        }
        _root.$message({
            showClose: true,
            message: "网络错误！请刷新后重试",
            type: 'error'
        });
        return Promise.reject(err);
    }
)
const http = {
    request(options: any) {
        return instance(options)
    },
    get(url: string, params: any) {
        loading = _root.$loading({
            lock: true,
            text: 'Loading',
            spinner: 'el-icon-loading',
            background: 'rgba(255,255,255, 0.3)'
        });
        if (url != '/python/login') {
            params.tocken = session.fetch('tocken')
        }
        return instance.get(url, {
            params: {
                params: params
            }
        })
    },
    post(url: string, data: any) {
        loading = _root.$loading({
            lock: true,
            text: '加载中...',
            spinner: 'el-icon-loading',
            background: 'rgba(255,255,255, 0.3)'
        });
        if (session.fetch('tocken')) {
            data.tocken = session.fetch('tocken')
        }
        return instance.post(url, data)
    },
    put(url: string, data: any) {
        loading = _root.$loading({
            lock: true,
            text: '加载中...',
            spinner: 'el-icon-loading',
            background: 'rgba(255,255,255, 0.3)'
        });
        return instance.put(url, data)
    },
    delete(url: string, data: any) {
        loading = _root.$loading({
            lock: true,
            text: 'Loading',
            spinner: 'el-icon-loading',
            background: 'rgba(255,255,255, 0.3)'
        });
        return instance.delete(url, data)
    },
    down(url: string) {
        loading = _root.$loading({
            lock: true,
            text: 'Loading',
            spinner: 'el-icon-loading',
            background: 'rgba(255,255,255, 0.3)'
        });
        axios.get(baseUrl + url, {
            params: {
                params: {
                    action: "exportData",
                    limit: 10,
                    page: 1,
                    tocken: session.fetch('tocken')
                }
            }
        })
            .then((res) => {
                loading.close()
                let tempLink: any = document.createElement('a');
                tempLink.setAttribute('download', res.data.url.slice(19));
                tempLink.setAttribute('href', '' + res.data.url);
                document.body.appendChild(tempLink);
                tempLink.click();
                tempLink.parentElement.removeChild(tempLink);
            })
            .catch(() => {
                _root.$message({
                    showClose: true,
                    message: "网络错误！请刷新后重试",
                    type: 'error'
                });
            });
    },
    update(url: string, file: any) {
        // loading = _root.$loading({
        //     lock: true,
        //     text: '加载中...',
        //     spinner: 'el-icon-loading',
        //     background: 'rgba(255,255,255, 0.3)'
        // });
        let formData = new FormData();
        formData.append('file', file);
        formData.append('params', JSON.stringify({
            action: "importData",
            tocken: session.fetch('tocken')
        }));
        axios.post(baseUrl + url, formData,
            {headers: {'Content-Type': 'multipart/form-data'}
        }).then(function (response) {
            // console.log(response);
        }).catch(function (error) {
            // console.log(error);
        });
    },
    baseurl: baseUrl
}
export default http