





import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import tableinfo from "@/components/table/index.vue";
@Component({
  components: {
    tableinfo
  }
})
export default class UserMsg extends Vue {
  private searchdata: any = {
    action: "findData",
    limit: 10,
    sortJson: { log_time: -1 }
  };
  public option: any = {
    searchForm: [
      {
        label: '用户名',
        prop: 'username',
        type: 'input'
      },
      {
        label: 'IP',
        prop: 'client_ip',
        type: 'input'
      },
      {
        label: '操作类型',
        prop: 'operation_type',
        type: 'selected',
        options: [
          {
            label: ''
          },
          {
            label: '退出'
          },
          {
            label: '登陆'
          },
          {
            label: '创建项目'
          },
          {
            label: '取消操作'
          }
        ]
      }
    ],
    searchOperate: [
      {
        type: "add",
        icon: "el-icon-search",
        label: "查询",
        setData: (data: any) => {
          if (data.log_time) {
            data.log_time = this.$moment(data.log_time).format('YYYY-MM-DD');
          }
          if (data.page) {
            data.page = 1
          }
          this.refreshData({
            whereJson: data
          });
        }
      }
    ],
    tableCol: [
      {
        prop: "username",
        label: "用户",
        width: 100
      },
      {
        prop: "client_ip",
        label: "IP",
        width: 130
      },
      {
        prop: "operation_type",
        label: "操作类型",
        width: 100
      },
      {
        prop: "operation_msg",
        label: "操作信息",
        width: 500
      },
      {
        prop: "log_time",
        label: "时间",
        width: 200
      }
    ],
    pageSize: 10,
    pageLimit: 10,
    tableData: []
  };
  private async changePage(k: number) {
    this.refreshData({
      page: k
    });
  }
  private async refreshData(obj: any) {
    for (let k in obj) {
      this.searchdata[k] = obj[k];
    }
    let res = await this.$http.get("/python/operation_list", this.searchdata);
    this.option.pageSize = res.count;
    this.option.tableData = res.rows;
  }
  async mounted() {
    console.log(this)
    this.refreshData({
      page: 1
    });
  }
}
