






















import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import tableinfo from "@/components/table/index.vue";
import session from "@/utils/sessionStorage";
import lineTab from "@/components/table/line.vue";
@Component({
  components: {
    tableinfo,
    lineTab
  }
})
export default class Dhcp extends Vue {
  private isdialogshow: boolean = false;
  private dialogtitle: string = "";
  private content = [
    {
      name: "租期/h",
      type: "line",
      data: []
    }
  ];
  private rulers: string[] = [];
  private color: string[] = ["#FFB6C1", "#7B68EE"];
  public option: optionObj = {
    tableCol: [
      {
        prop: "lease",
        label: "Lease",
        width: 200
      },
      {
        prop: "client_ip",
        label: "IP",
        width: 300
      },
      {
        prop: "client_host",
        label: "HOSTNAME",
        width: 200
      },
      {
        prop: "client_mac",
        label: "MAC",
        width: 200
      },
      {
        prop: "log_time",
        label: "Time",
        width: 200
      }
    ],
    pageSize: 10,
    pageLimit: 10,
    tableData: []
  };

  async mounted() {
    let res = await this.$http.get("/python/dhcp_record_list", {
      action: "findData",
      limit: 10,
      page: 1,
      whereJson: {},
      sortJson: { log_time: -1 }
    });
    for (let i = 0; i < res.rows.length; i++) {
      res.rows[i].lease = this.leasetoMin(res.rows[i].lease);
    }
    this.option.tableData = res.rows;
    this.option.pageSize = res.count;
  }
  private searchtime: Date = new Date();
  private leasetoMin(leaset: number): string {
    let h = Math.floor(leaset / 3600);
    let min = Math.floor((leaset - h * 3600) / 60);
    return `${h}h${min}min`;
  }
  private async changePage(k: number) {
    let res = await this.$http.get("/python/dhcp_record_list", {
      action: "findData",
      limit: 10,
      page: k,
      whereJson: {},
      sortJson: { log_time: -1 }
    });
    for (let i = 0; i < res.rows.length; i++) {
      res.rows[i].lease = this.leasetoMin(res.rows[i].lease);
    }
    this.option.tableData = res.rows;
  }
  private client_ip: string = "";
  private async searchByTag() {
    let time = this.searchtime;
    let month = time.getMonth() + 1;
    let getmonth = month > 9 ? month : "0" + month;
    let date = time.getDate() > 9 ? time.getDate() : "0" + time.getDate();
    let res = await this.$http.get("/python/dhcp_record_list", {
      action: "findData",
      whereJson: {
        client_ip: this.client_ip,
        time: `${time.getFullYear()}-${getmonth}-${date}`
      }
    });
    for (let i = 0; i < res.info.lease.length; i++) {
      res.info.lease[i] = (res.info.lease[i] / 3600).toFixed(2);
    }
    this.isdialogshow = true;
    this.content[0].data = res.info.lease;
    this.rulers = res.info.time;
    this.isReDraw++;
  }
  private isReDraw: number = 0;
  private async bechoiced(data: any) {
    this.dialogtitle = data.client_mac;
    this.client_ip = data.client_ip;
    let res = await this.$http.get("/python/dhcp_record_list", {
      action: "findData",
      whereJson: { client_ip: data.client_ip, time: data.log_time.slice(0, 10) }
    });
    for (let i = 0; i < res.info.lease.length; i++) {
      res.info.lease[i] = (res.info.lease[i] / 3600).toFixed(2);
    }
    this.isdialogshow = true;
    this.content[0].data = res.info.lease;
    this.rulers = res.info.time;
  }
}
interface optionObj {
  tableCol: any;
  pageSize: any;
  pageLimit: any;
  tableData: any;
}
