





import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import graph from "@/components/table/graph.vue";
@Component({
  components: {
    graph
  }
})
export default class UserMsg extends Vue {
  private data: any[] = [];
  public async mounted() {
    let res = await this.$http.get("/python/device_list", {
      action: "findData",
      limit: 10000,
      page: 1
    });
    for (let i = 0; i < res.rows.length; i++) {
      this.data = res.rows;
    }
  }
}
