














import Vue from 'vue'
import tableinfo from '@/components/table/index.vue'

export default Vue.extend({
  components: {
    tableinfo
  },
  data() {
    return {
      curPage: 1,
      option2: null,
      option3: null,
      activeName: 'first',
      mailList: [],
      option: {
        options: [
          {
            type: "add",
            icon: "el-icon-plus",
            label: "新增",
            setData: async (data: any) => {
              let res = await this.$http.post("/python/email_user_list", {
                action: "insertData",
                dataArr: [data]
              });
              if (res) {
                this.$message({
                  type: "success",
                  message: "新增成功!"
                });
                let res1 = await this.$http.get('/python/email_user_list', {
                  action: 'findData'
                });
                (this as any).option.tableData = res1.rows;
              }
            }
          }
        ],
        tableCol: [
          {
            prop: "email",
            label: "地址",
            width: 130,
            rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
          },
          {
            prop: "message",
            label: "备注",
            width: 100
          },
        ],
        tableColOperate: [
          {
            label: "删除",
            type: "danger",
            setData: async (data: any) => {
              let res = await this.$http.post("/python/email_user_list", {
                action: "delData",
                whereJson: { id: [data.id] }
              });
              if (res) {
                this.$message({
                  type: "success",
                  message: "删除成功!"
                });
                // let res1 = await this.$http.get('/python/email_user_list', {
                //   action: 'findData'
                // });
                // (this as any).option.tableData = res1.rows;
                (this as any).curPage = 1;
                (this as any).this.getMailList();
              }
            }
          }
        ],
        tableData: []
      },
    }
  },
  methods: {
    async getMailList () {
      let res = await this.$http.get('/python/email_user_list', {
        action: 'findData',
        page: this.curPage
      });
      this.option.tableData = res.rows;
    },
    async getSendList () {
      let res = await this.$http.get('/python/')
    }
  },
  created() {
    this.getMailList();
  }
})
