



































import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import login from "@/views/system/login.vue";
import sessionStorage from "../../utils/sessionStorage";
@Component
export default class Login extends Vue {
  private form: any = {};
  private async login() {
    this.form.action = "SignIn";
    let res = await this.$http.post("python/login", this.form);
    sessionStorage.save("tocken", res.tocken);
    sessionStorage.save("name", this.form.username);
    this.$message({
      showClose: true,
      message: "登录成功",
      type: "success"
    });
    this.$router.back();
  }
  private codeimg: string = "";
  private rules: object = {
    username: { required: true, message: "请输入用户名", trigger: "blur" },
    password: { required: true, message: "请输入密码", trigger: "blur" },
    code: { required: true, message: "请输入验证码", trigger: "blur" }
  };
}
