






import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import tableinfo from "@/components/table/index.vue";
@Component({
  components: {
    tableinfo
  }
})
export default class equManagement extends Vue {
  public option: any = {
    options: [
      {
        type: "add",
        icon: "el-icon-plus",
        label: "新增",
        setData: async (data: any) => {
          let res = await this.$http.post("/python/device_list", {
            action: "insertData",
            dataArr: [data]
          });
          if (res) {
            this.$message({
              type: "success",
              message: "新增成功!"
            });
            this.refreshData(1);
          }
        }
      }
    ],
    tableCol: [
      {
        prop: "name",
        label: "设备名",
        width: 100,
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "address",
        label: "地址",
        width: 200,
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "code",
        label: "编号",
        width: 130,
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "cpu",
        label: "CPU",
        width: 100,
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "device_type",
        label: "设备类型",
        width: 100,
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "disk",
        label: "硬盘",
        width: 100,
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "factory",
        label: "厂家",
        width: 100,
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "ip",
        label: "IP",
        width: 150,
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "level",
        label: "所属地市",
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "message",
        label: "描述信息",
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "online_time",
        label: "在线时间",
        width: 100,
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "os",
        label: "操作系统",
        width: 100,
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "os_version",
        label: "系统版本",
        width: 100,
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "power",
        label: "电压",
        width: 100,
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      },
      {
        prop: "ram",
        label: "RAM",
        width: 100,
        rules: [{ required: true, message: "该项为必填", trigger: "blur" }]
      }
    ],
    pageSize: 5,
    pageLimit: 5,
    tableColOperate: [
      {
        label: "修改",
        setData: async (data: any) => {
          let name = data.name;
          delete data.name;
          delete data.ip;
          let res = await this.$http.post("/python/device_list", {
            action: "updateData",
            whereJson: { name: name },
            updateJson: data
          });
          if (res) {
            this.$message({
              type: "success",
              message: "修改成功!"
            });
            this.refreshData(1);
          }
        }
      },
      {
        label: "删除",
        type: "danger",
        setData: async (data: any) => {
          let res = await this.$http.post("/python/device_list", {
            action: "delData",
            whereJson: { id: [data.id] }
          });
          if (res) {
            this.$message({
              type: "success",
              message: "删除成功!"
            });
            this.refreshData(1);
          }
        }
      }
    ],
    tableData: []
  };
  private async refreshData(k: number) {
    let res = await this.$http.get("/python/device_list", {
      action: "findData",
      limit: 10,
      page: k
    });
    this.option.pageSize = res.count;
    this.option.tableData = res.rows;
  }
  private async changePage(k: number) {
    this.refreshData(k);
  }
  async created() {
    this.refreshData(1);
  }
}
