






import Vue from 'vue'
import tableinfo from '@/components/table/index.vue'

export default Vue.extend({
  components: {
    tableinfo
  },
  data() {
    return {
      option: {
        tableCol: [
          {
            prop: "alarm_type",
            label: "类型",
            width: 130,
            disabled: true
          },
          {
            prop: "symbol",
            label: "操作符",
            width: 100,
            disabled: true
          },
          {
            prop: "tvalue",
            label: "阈值",
            width: 100
          },
          {
            prop: "unit",
            label: "单位",
            width: 100,
            disabled: true
          },
          {
            prop: "status",
            label: "状态",
            width: 100,
            type: 'selected',
            options: [{
              label: 'ON',
              value: 'ON'
            }, {
              label: 'OFF',
              value: 'OFF'
            }]
          },
        ],
        tableColOperate: [
          {
            label: "修改",
            setData: async (data: any) => {
              let id = data.id;
              delete data.alarm_type;
              delete data.symbol;
              delete data.unit;
              delete data.id;
              delete data.status_value;
              let res = await this.$http.post("/python/alarm_type_list", {
                action: "updateData",
                whereJson: { id: id },
                updateJson: data
              });
              if (res) {
                this.$message({
                  type: "success",
                  message: "修改成功!"
                });
                (this as any).getThresholdData();
              }
            }
          }
        ],
        tableData: []
      }
    }
  },
  methods: {
    async getThresholdData () {
      let res = await this.$http.get('/python/alarm_type_list', {
        action: 'findData'
      });
      this.option.tableData = res.rows;
    }
  },
  created() {
    this.getThresholdData();
  }
})
