


















import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import session from "@/utils/sessionStorage";
import pie from "@/components/table/pie.vue";
@Component({
  components: {
    pie
  }
})
export default class UserMsg extends Vue {
  private activeName: string = "first";
  private piedata: any[] = [];
  private piedata1: any[] = [];
  async mounted() {
    let res = await this.$http.get("/python/client_type_list", {
      action: "findData"
    });
    for (let i = 0; i < res.info.rate.length; i++) {
      this.piedata.push({
        name: res.info["os_type"][i],
        value: res.info["rate"][i]
      });
    }
  }
}
