



//饼图
import { Component, Prop, Vue, Watch } from "vue-property-decorator";
@Component
export default class extends Vue {
  get lineclass() {
    return "line" + Math.floor(Math.random() * 10000);
  }
  // @param canvasWidth 宽
  // @param canvasHeight 高
  // @param data 数据
  // @param title 标题
  // @param isReDraw 刷新次数 需要刷新就+1
  @Prop() private canvaswidth!: string;
  @Prop() private canvasheight!: string;
  @Prop() private title!: string;
  @Prop() private data!: any[];
  @Prop() private isReDraw!: number;
  //  watch: {
  //   isReDraw(oldval, newval) {
  //     this.drawLine();
  //   }
  // },
  @Watch("isReDraw") private change(newTxt: number, oldTxt: number) {
    this.draw();
  }
  private draw() {
    const echarts = require("echarts");
    const options = {
      title: {
        text: this.title,
        x: "center"
      },
      tooltip: {
        trigger: "item",
        formatter: "{b} : {c} ({d}%)"
      },
      legend: {
        orient: "vertical",
        left: "left",
        data: this.getLegend(this.data)
      },
      series: [
        {
          type: "pie",
          radius: "55%",
          center: ["50%", "60%"],
          data: this.data,
          itemStyle: {
            emphasis: {
              shadowBlur: 10,
              shadowOffsetX: 0,
              shadowColor: "rgba(0, 0, 0, 0.5)"
            }
          }
        }
      ]
    };
    echarts.init(document.getElementById(this.lineclass)).clear();
    const myChart = echarts.init(document.getElementById(this.lineclass));
    myChart.setOption(options);
  }
  private getLegend(arr: any[]) {
    let arr1: string[] = [];
    for (let i = 0; i < arr.length; i++) {
      arr1.push(arr[i].name);
    }
  }
  mounted() {
    setTimeout(() => {
      this.draw();
    }, 500);
  }
}
