
























import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import tableinfo from "@/components/table/index.vue";
import session from "@/utils/sessionStorage";
import lineTab from "@/components/table/line.vue";
@Component({
  components: {
    tableinfo,
    lineTab
  }
})
export default class Dhcp extends Vue {
  private isdialogshow: boolean = false;
  private dialogtitle: string = "";
  private searchtime: Date = new Date();
  private content = [
    {
      name: "",
      type: "line",
      data: []
    }
  ];
  private isReDraw: number = 1;
  private rulers: string[] = [];
  private color: string[] = ["#FFB6C1", "#7B68EE"];
  public option: optionObj = {
    tableCol: [
      {
        prop: "client_ip",
        label: "Network",
        width: 200
      },
      {
        prop: "vlan_name",
        label: "VLAN Name",
        width: 200
      },
      {
        prop: "address_rate",
        label: "IP Utilization",
        width: 130
      },
      {
        prop: "vlan_id",
        label: "VlanID",
        width: 150
      },
      {
        prop: "vrf_name",
        label: "VRF Name",
        width: 150
      },
      {
        prop: "log_time",
        label: "Time",
        width: 200
      }
    ],
    pageSize: 10,
    pageLimit: 10,
    tableData: []
  };

  async mounted() {
    let res = await this.$http.get("/python/address_rate_list", {
      action: "findData",
      limit: 10,
      page: 1,
      sortJson: { log_time: -1 }
    });
    for (let i = 0; i < res.rows.length; i++) {
      res.rows[i].address_rate =
        (res.rows[i].address_rate * 100).toFixed(0) + "%";
    }
    this.option.tableData = res.rows;
    this.option.pageSize = res.count;
  }
  private async changePage(k: number) {
    let res = await this.$http.get("/python/address_rate_list", {
      action: "findData",
      limit: 10,
      page: k,
      sortJson: { log_time: -1 }
    });
    this.option.tableData = res.rows;
  }
  private async searchByTag() {
    let time = this.searchtime;
    let month = time.getMonth() + 1;
    let getmonth = month > 9 ? month : "0" + month;
    let date = time.getDate() > 9 ? time.getDate() : "0" + time.getDate();
    let res = await this.$http.get("/python/address_rate_list", {
      action: "findData",
      limit: 10,
      page: 1,
      whereJson: {
        client_ip: this.clientip,
        time: `${time.getFullYear()}-${getmonth}-${date}`
      }
    });
    this.isdialogshow = true;
    this.content[0] = {
      name: this.dialogtitle,
      type: "line",
      data: res.info.address_rate.map((item:any) => {
        return item * 100
      })
    };
    this.rulers = res.info.time;
    this.isReDraw++;
  }
  private clientip: string = "";
  private async bechoiced(data: any) {
    console.log(data, 'aaaaaaaaaaaa')
    this.dialogtitle = data.vlan_name;
    this.clientip = data.client_ip;
    let res = await this.$http.get("/python/address_rate_list", {
      action: "findData",
      limit: 10,
      page: 1,
      whereJson: { client_ip: data.client_ip, time: data.log_time.slice(0, 10) }
    });
    this.isdialogshow = true;
    this.content[0] = {
      name: data.vlan_name,
      type: "line",
      data: res.info.address_rate.map((item:any) => {
        return item * 100
      })
    };
    this.rulers = res.info.time;
    this.isReDraw++;
  }
}
interface optionObj {
  tableCol: any;
  pageSize: any;
  pageLimit: any;
  tableData: any;
}
