





import { Component, Prop, Vue, Watch } from "vue-property-decorator";
import tableinfo from "@/components/table/index.vue";
@Component({
  components: {
    tableinfo
  }
})
export default class UserMsg extends Vue {
  private searchdata: any = {
    action: "findData",
    limit: 10,
    page: 1,
    sortJson: { log_time: -1 }
  };
  public option: any = {
    searchForm: [
      {
        label: 'IP',
        prop: 'client_ip',
        type: 'input'
      }
    ],
    searchOperate: [
      {
        type: "add",
        icon: "el-icon-search",
        label: "查询",
        setData: (data: any) => {
          if (data.page) {
            data.page = 1
          }
          this.refreshData({
            whereJson: data
          });
        }
      }
    ],
    tableCol: [
      {
        prop: "client_ip",
        label: "IP",
        width: 150
      },
      {
        prop: "cpu_rate",
        label: "CPU使用率",
        width: 100
      },
      {
        prop: "net_flow_receive",
        label: "接收流量(Kb)",
        width: 140
      },
      {
        prop: "net_flow_send",
        label: "发送流量(Kb)",
        width: 120
      },
      {
        prop: "net_speed",
        label: "网速(Kb/s)",
        width: 120
      },
      {
        prop: "ram_rate",
        label: "内存使用率",
        width: 130
      },
      {
        prop: "log_time",
        label: "Time",
        width: 200
      }
    ],
    pageSize: 10,
    pageLimit: 10,
    tableData: []
  };
  private async changePage(k: number) {
    this.refreshData({
      page: k
    });
  }
  private async refreshData(obj: any) {
    for (let k in obj) {
      this.searchdata[k] = obj[k];
    }
    let res = await this.$http.get("/python/device_run_list", this.searchdata);
    for (let i = 0; i < res.rows.length; i++) {
      res.rows[i].cpu_rate = (res.rows[i].cpu_rate * 100).toFixed(0) + "%";
      res.rows[i].ram_rate = (res.rows[i].ram_rate * 100).toFixed(0) + "%";
    }
    this.option.pageSize = res.count;
    this.option.tableData = res.rows;
  }
  async mounted() {
    this.refreshData({
      page: 1
    });
  }
}
